.descriptionBlock{
    padding: 50px;
    display: flex;
    justify-content: center;
    justify-content: space-around;
}

.imgBlock{
    display: flex;
    flex-direction: column;
}

.mainPhoto img{
    max-width:250px;
    max-height: 250px;
    border-radius: 150px;
}

.changePhoto{
    padding: 10px;
}

.changePhoto input::-webkit-file-upload-button {
    content: "ss";
    font-family: 'Montserrat', sans-serif;
    color: black;
    content: "Choose a file";
    width: auto;
    height: 40px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    background-color: white;
}

.changePhoto input::-webkit-file-upload-button:hover {
    opacity: 0.8;
}

.changePhoto input[type='file'] {
    color: transparent;
    margin-left: 40px;
}

.infoBlock{
    font-size: 24px;
    border: 5px solid black;
    background-color: white;
    padding: 10px;
}
