.profileForm {
    padding: 10px;
}

.profileForm input, .profileForm textarea, .changeContact input {
    font-size: 18px;
}

.contact {
    padding-left: 10px;
    padding-top: 5px;
    font-size: 18px;
}

.changeContact {
    padding-left: 10px;
    padding-top: 5px;
}

.saveBtn {
    text-align: center;
    margin-top: 20px;
}

.saveBtn button {
    font-family: 'Montserrat', sans-serif;
    color: white;
    width: 100px;
    height: 40px;
    font-size: 18px;
    border: none;
    cursor: pointer;
    background-color: green;
}

.saveBtn button:hover {
    opacity: 0.8;
}