.profileBlock{
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.btnBlock{
    display: flex;
    justify-content: center;
    padding: 10px;
}

.editInfoBtn button{
    font-family: 'Montserrat', sans-serif;
    color: white;
    width: 100px;
    height: 40px;
    font-size: 24px;
    border: none;
    cursor: pointer;
    background-color: blue;
}

.editInfoBtn button:hover {
    opacity: 0.8;
}