.postForm {
    display: flex;
    flex-direction: column;
}

.postText, .addPostBtn {
    display: flex;
    justify-content: center;
}

.postText textarea {
    height:100px;
    width: 500px;
    font-size: 18px;
}

.addPostBtn {
    padding: 10px;
}

.addPostBtn button {
    font-family: 'Montserrat', sans-serif;
    color: white;
    width: 100px;
    height: 40px;
    font-size: 18px;
    margin-left: 407px;
    border: none;
    cursor: pointer;
    background-color: green;
}

.addPostBtn button:hover {
    opacity: 0.8;
}