.item {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin-right: 400px;
}

.itemImg {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.itemMessage{
    padding: 5px;
}

.itemImg, .itemLikes {
    display: flex;
    justify-content: center;
    padding: 5px;
}

.itemImg img {
    width: 60px;
    height: 60px;
    border-radius: 20px;
}


